.sittings-table {
    background: #1F1D2B;
    border-radius: 8px;
    width: 850px;
    box-sizing: border-box;
    &-top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 24px;
        h1 {
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 140%;
            color: #FFFFFF;
        }
        button {
            background: #1F1D2B;
            border: 1px solid #393C49;
            box-sizing: border-box;
            border-radius: 8px;
            padding: 14px;
            display: flex;
            align-items: center;
            cursor: pointer;
            transition: all ease 0.5s;
            &:hover {
                background: #EA7C69;
                color: #fff;
                box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.4);
            }
            span {
                margin-right: 10px;
            }
            h5 {
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 140%;
                color: #FFFFFF;
            }
        }
    }
    &-navbar {
        display: flex;
        border-bottom: 1px solid #393C49;
        padding: 24px;
        padding-bottom: 0;
        button {
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 140%;
            color: #FFFFFF;
            position: relative;
            padding: 0;
            cursor: pointer;
            padding-bottom: 12px;
            outline: none;
            border: none;
            background-color: transparent;
            margin-right: 32px;
            &::before {
                content: "";
                width: 0;
                height: 3px;
                position: absolute;
                bottom: -2.5px;
                left: 0;
                background: #EA7C69;
                transition: all ease 0.5s;
                border-radius: 30px;
            }
            &:hover, &.active {
                color: #EA7C69;
                &::before {
                    width: 70%;
                }
            }
        }
    }
    &-blocks {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding: 24px;
        height: 350px;
        overflow-y: scroll;
        &::-webkit-scrollbar {
            display: none;
        }
        &-block {
            width: 222px;
            box-sizing: border-box;
            background: #1F1D2B;
            border: 1px dashed #393C49;
            border-radius: 8px;
            margin-bottom: 16px;
            text-align: center;
            img {
                width: 144px;
                height: 130px;
                object-fit: contain;
                padding: 20px;
                box-sizing: border-box;
            }
            h4 {
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
                text-align: center;
                color: #FFFFFF;
                width: 144px;
                margin: auto;
                margin-bottom: 5px;
                text-align: center;
            }
            h5 {
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 140%;
                text-align: center;
                color: #ABBBC2;
                margin-bottom: 16px;
            }
            button {
                display: flex;
                align-items: center;
                justify-content: center;
                background: rgba(234, 124, 105, 0.2);
                border-radius: 0px 0px 8px 8px;
                border: none;
                outline: none;
                padding: 16px;
                text-align: center;
                box-sizing: border-box;
                width: 100%;
                cursor: pointer;
                &:hover {
                    background: #EA7C69;
                    color: #fff;
                    box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.4);
                    span {
                        svg {
                            fill: #fff;
                        }
                    }
                    h6 {
                        color: #fff;
                    }
                }
                span {
                    svg {
                        fill: #EA7C69;
                    }
                }
                h6 {
                    font-style: normal;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 140%;
                    color: #EA7C69;
                    margin-left: 10px;
                }
            }
        }
    }
    .buttons {
        display: flex;
        align-items: center;
        margin: 16px 24px;
        margin-bottom: 0;
        button {
            cursor: pointer;
            background-color: transparent;
            border-radius: 8px;
            margin-right: 16px;
            border: none;
            outline: none;
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 140%;
            color: #EA7C69;
            width: 175px;
            box-sizing: border-box;
            border: 1px solid #EA7C69;
            padding: 14px;
            &:hover, &.active {
                background: #EA7C69;
                box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.4);
                border-radius: 8px;
                color: #fff;
            }
        }
    }
}