.sittings-sidebar {
    background: #1F1D2B;
    border-radius: 8px;
    margin: 0 24px;
    width: 275px;
    padding-bottom: 70px;
    &-block {
        display: flex;
        padding: 24px;
        cursor: pointer;
        position: relative;
        &::before {
            content: "";
            width: 4px;
            height: 0px;
            background: #EA7C69;
            border-radius: 30px;
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            margin: auto;
            transition: all ease 0.4s;
        }
        &:hover {
            &::before {
                height: 50px;
            }
            background: rgba(234, 124, 105, 0.26);
            border-radius: 8px;
            .sittings-sidebar-block-left {
                span {
                    svg {
                        fill: #EA7C69;
                    }
                }
            }
            .sittings-sidebar-block-right {
                h4 {
                    color: #EA7C69;
                }
                h5 {
                    color: #ABBBC2;
                }
            }
        }
        &-left {
            span {
                margin-right: 10px;
                width: 15px;
                height: 15px;
                object-fit: contain;
                svg {
                    fill: #ABBBC2;
                }
            }
        }
        &-right {
            h4 {
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
                color: #FFFFFF;
                margin-bottom: 5px;
            }
            h5 {
                font-style: normal;
                font-weight: normal;
                font-size: 12px;
                line-height: 140%;
                color: #ABBBC2;
            }
        }
    }
}