.orders {
    width: 410px;
    background: #1F1D2B;
    border-radius: 8px;
    padding: 24px 24px 0 24px;
    color: #fff;
    box-sizing: border-box;
    position: relative;
    height: 100vh;
    box-sizing: border-box;
    &-id {
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 140%;
        color: #FFFFFF;
        margin-bottom: 24px;
    }
    &-buttons {
        margin-bottom: 24px;
        .orders-btn {
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 140%;
            color: #EA7C69;
            background: #1F1D2B;
            border: 1px solid #393C49;
            box-sizing: border-box;
            border-radius: 8px;
            padding: 7px 12px;
            margin-right: 10px;
            cursor: pointer;
            outline: none;
            &.active, &:hover {
                color: #fff;
                background: #EA7C69;
            }
        }
    }
    &-item {
        display: flex;
        justify-content: space-between;
        padding-bottom: 24px;
        border-bottom: 1px solid #393C49;
        .iqp {
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 140%;
            color: #FFFFFF;
        }
        &-left {
            display: flex;
            justify-content: space-between;
            &-item {
                margin-right: 225px;
            }
        }
    }
    &-main {
        height: 450px;
        overflow-y: scroll;
        &::-webkit-scrollbar {
            width: 0 !important;
            height: 0;
            display: none !important;
            background: transparent;
        }
        &-main {
            display: flex;
            justify-content: space-between;
            align-items: center;
            &-left {
                &-top {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    margin-top: 24px;
                    &-left {
                        display: flex;
                        img {
                            width: 45px;
                            height: 41px;
                            object-fit: contain;
                            margin-right: 5px;
                        }
                        div {
                            h5 {
                                font-style: normal;
                                font-weight: 500;
                                font-size: 14px;
                                line-height: 130%;
                                color: #FFFFFF;
                                margin-bottom: 5px;
                            }
                            h6 {
                                font-style: normal;
                                font-weight: 500;
                                font-size: 12px;
                                line-height: 140%;
                                color: #ABBBC2;
                            }
                        }
                        margin-bottom: 15px;
                    }
                    &-right {
                        span {
                            font-style: normal;
                            font-weight: 500;
                            font-size: 16px;
                            line-height: 140%;
                            color: #FFFFFF;
                            background: #2D303E;
                            border: 1px solid #393C49;
                            box-sizing: border-box;
                            border-radius: 8px;
                            padding: 12px;
                        }
                    }
                }
                &-bottom {
                    input {
                        font-style: normal;
                        font-weight: normal;
                        font-size: 14px;
                        line-height: 140%;
                        color: #E0E6E9;
                        background: #2D303E;
                        border: 1px solid #393C49;
                        box-sizing: border-box;
                        border-radius: 8px;
                        padding: 14px;
                        width: 280px;
                        border: none;
                        outline: none;
                    }
                }
            }
            &-right {
                h4 {
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 140%;
                    text-align: right;
                    color: #FFFFFF;
                    margin-bottom: 12px;
                }
                button {
                    fill: #FF7CA3;
                    padding: 8px 12px;
                    border: 1px solid #FF7CA3;
                    border-radius: 8px;
                    cursor: pointer;
                    background-color: transparent;
                    outline: none;
                    &.active, &:hover {
                        border: 1px solid #FFF;
                        fill: #fff;
                    }
                }
            }
        }
    }
    &-bottom {
        background-color: #1F1D2B;
        padding: 20px 24px 20px 0;
        position: absolute;
        bottom: 0;
        width: 385px;
        box-sizing: border-box;
        &-top {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 12px;
            h4 {
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 140%;
                color: #ABBBC2;
            }
            h3 {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 140%;
                text-align: right;
                color: #FFFFFF;
            }
        }
        button {
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 140%;
            color: #FAFAFA;
            background: #EA7C69;
            border-radius: 8px;
            display: block;
            box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.1);
            width: 100%;
            padding: 14px;
            text-align: center;
            border: none;
            outline: none;
            cursor: pointer;
            &:hover {
                box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.7);
            }
        }
    }
    &-modal {
        position: absolute;
        top: 0;
        left: -955px;
        width: 333%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.3);
        display: none;
        &.active {
            display: block;
        }
        &.notActive {
            display: none;
        }
        &-payment {
            position: absolute;
            top: 0;
            right: 0;
            width: 30.05%;
            box-sizing: border-box;
            background: #1F1D2B;
            border-radius: 8px;
            padding: 50px 24px 24px 24px;
            .payment {
                font-style: normal;
                font-weight: 600;
                font-size: 28px;
                line-height: 140%;
                color: #FFFFFF;
                margin-bottom: 12px;
            }
            .payment-text {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 140%;
                color: #ABBBC2;
                padding-bottom: 24px;
                border-bottom: 1px solid #393C49;
                margin-bottom: 24px;
            }
            .payment-method {
                font-style: normal;
                font-weight: 600;
                font-size: 20px;
                line-height: 140%;
                color: #FFFFFF;
                margin-bottom: 16px;
            }
            .payment-card {
                display: flex;
                .card-button {
                    display: block;
                    width: 105px;
                    background: #1F1D2B;
                    border: 1px solid #393C49;
                    border-radius: 8px;
                    padding: 10px 15px;
                    box-sizing: border-box;
                    text-align: center;
                    fill: #ABBBC2;
                    margin-right: 10px;
                    outline: none;
                    cursor: pointer;
                    span {
                        margin-top: 5px;
                        display: block;
                        font-style: normal;
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 130%;
                        color: #ABBBC2;
                    }
                    &.active {
                        fill: #fff;
                        background: #252836;
                        border: 1px solid #ABBBC2;
                        span {
                            color: #fff;
                        }
                    }
                }
            }
            .payment-input {
                label {
                    display: block;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 130%;
                    color: #FFFFFF;
                    margin-bottom: 10px;
                    margin-top: 16px;
                }
                input {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 140%;
                    color: #E0E6E9;
                    background: #2D303E;
                    border: 1px solid #393C49;
                    box-sizing: border-box;
                    border-radius: 8px;
                    outline: none;
                    padding: 12px 14px;
                    width: 100%;
                }
            }
            .payment-date {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding-bottom: 16px;
                border-bottom: 1px solid #393C49;
                margin-bottom: 16px;
                margin-top: 24px;
                .date-left {
                    label {
                        display: block;
                        font-style: normal;
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 130%;
                        color: #FFFFFF;
                        margin-bottom: 10px;
                    }
                    .input {
                        font-style: normal;
                        font-weight: normal;
                        font-size: 14px;
                        line-height: 140%;
                        color: #E0E6E9;
                        background: #2D303E;
                        border: 1px solid #393C49;
                        box-sizing: border-box;
                        border-radius: 8px;
                        width: 170px;
                        padding: 14px;
                        outline: none;
                        border: none;
                    }
                }
            }
            .order-type {
                border: none;
            }
            .payment-bottom-btn {
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-top: 50px;
                button {
                    font-style: normal;
                    font-weight: 600;
                    width: 178px;
                    cursor: pointer;
                    font-size: 14px;
                    line-height: 140%;
                    color: #EA7C69;
                    background: transparent;
                    border-radius: 8px;
                    padding: 14px 28px;
                    border: 1px solid #EA7C69;
                    outline: none;
                    transition: all ease 0.5s;
                    &.active, &:hover {
                        background: #EA7C69;
                        box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.4);
                        color: #FAFAFA;
                        transform: scale(1.03);
                    }
                }
            }
        }
    }
}