.hot-dishes {
    display: flex;
    justify-content: space-between;
    padding: 0 24px;
    padding-top: 30px;
    position: relative;
    margin-top: 50px;
    flex-wrap: wrap;
    width: 100%;
    height: 575px;
    overflow-y: auto;
    box-sizing: border-box;
    &::-webkit-scrollbar {
        width: 0 !important;
        height: 0;
        display: none !important;
        background: transparent;
    }
    .block-dishes {
        width: 192px;
        padding:  0 24px 24px 24px;
        text-align: center;
        background-color: #1F1D2B;
        border-radius: 16px;
        margin-bottom: 70px;
        .dishes-food {
            width: 150px;
            height: 135px;
            object-fit: contain;
            position: relative;
            top: -30px;
        }
        h5, h6 {
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 140%;
            text-align: center;
            color: #FFFFFF;
            margin: 0 auto;
            margin-bottom: 10px;
            width: 144px;
        }
        .bowls {
            color: #ABBBC2;
            margin-bottom: 0;
        }
    }
}